@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap);
.vote-background{width:100%;height:calc(100% - 50px);font-family:"Black Han Sans", sans-serif;margin-top:50px}.vote-background .vote-question{width:100%;text-align:center;height:100px}.vote-background .vote-question .vote-title{font-size:30px;margin-top:10px;color:#e54b4b}.vote-background .vote-question p{font-size:20px;margin-top:10px}.vote-background .container{width:100%;display:flex;justify-content:center}.vote-background .container label{position:relative;color:#e54b4b;width:100%;font-family:"Poppins", sans-serif;font-size:30px;border:2px solid #e54b4b;border-radius:5px;padding:10px 50px;display:flex}.vote-background .container label::before{content:"";height:30px;width:30px;border:2px solid #e54b4b;border-radius:50%;margin-right:20px}.vote-background .container input[type="radio"]{display:none}.vote-background .container input[type="radio"]:checked+label{background:#e54b4b;color:white}.vote-background .container input[type="radio"]:checked+label:before{height:15px;width:15px;background:#e54b4b;border:10px solid white;border-radius:50%}.vote-background .container .vote-btn--submit{width:100%;height:60px;background:#5ecb66;border-radius:5px;border:0;color:white;font-size:40px}

